
.MuiPaper-root {
  color: #D0DCE8;
  transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  backdrop-filter: blur(33px);
}

.MuiPaper-root.ohm-card {
  width: 97%;
  padding: 20px 30px 30px 30px;
  overflow: hidden;
  max-width: 833px;
  border-radius: 10px;
  margin-bottom: 1.8rem;
}

.MuiPaper-root.ohm-modal {
  background-color: #24242699;
}

.MuiPaper-root.ohm-menu {
  margin: 0px;
  padding: 22px 0px;
  border-radius: 10px;
  backdrop-filter: blur(33px);
  background-color: rgba(36, 40, 49, 1);
}

.MuiPaper-root.ohm-popover {
  color: #D0DCE8;
  backdrop-filter: blur(15px);
  background-color: rgba(54, 56, 64, 0.99);
}

.MuiPaper-root.ohm-chart-card {
  width: 97%;
  padding: 20px 0px;
  max-width: 700px;
  white-space: nowrap;
  margin-bottom: 1.8rem;
}

.MuiPaper-rounded {
  border-radius: 4px;
}

.MuiPaper-outlined {
  border: 1px solid rgba(255, 255, 255, 0.12);
}

